<template>
    <div class="activity_card">
        <!-- <img :src="cardData.image" alt=""> -->
        <img src="../../../static/img/marketing/guanggao4_1.png" alt="" />
        <p>{{cardData.title}}</p>
        <div>
            <span @click="save_joinActivity()">参与</span>
            <span @click="refuse()">拒绝</span>
        </div>
    </div>
</template>
<script>
import { joinActivity}  from '@/api/marketing.js'
export default {
    props:{
        cardData:{
            type:Object,
            required:true
        }
    },
    methods:{
        refuse(){
            this.$emit('event1', '1')
        },
        save_joinActivity(){
            joinActivity(`?activityId=${this.cardData.id}`).then( res =>{
                if( res.code == 200){
                    this.$message({
                        message: '恭喜您，参与成功！',
                        type: 'success'
                    });
                    setTimeout(()=>{
                        this.refuse()
                    },1500)
                }
            })
        }
    }
}
</script>
<style lang="scss" scoped>
.activity_card{
    display: flex;
    flex-direction: column;
    align-content: center;
    min-width: 1000px;
    img{
        margin: 20px auto;
        height: 180px;
        width: 470px;
    }
    p{
        text-indent:35px;
        width: 80%;
        min-height: 200px;
        margin: 20px auto;
        color: #757575;
        font-size: 15px;
        line-height: 26px;
    }
    div{
        width: 36%;
        margin:  0 auto;
        display: flex;
        justify-content: space-around;
        span{
            width: 120px;
            height: 34px;
            line-height: 34px;
            text-align: center;
            color: #fff;
            background: #FF9B56;
            border-radius: 2px;
            cursor: pointer;
            font-size: 15px;
        }
        span:nth-child(2){
            background: #00AE78;
        }
    }
}
</style>>


